import React from "react";
import {
    useWindowWidth,
} from '@react-hook/window-size';

export default function Template({
  type
}) {
  const onlyWidth = useWindowWidth();
  const isMobile = onlyWidth <= 728;
  if (isMobile) {
    return (<div className="AdsContainer" id={"188970836"}></div>);
  }

  if (type === "leaderboard") {
    return (<div className="AdsContainer" id={"138821154"}></div>);
  }

  if (type === "blog-content") {
    return (<div className="AdsContainer" id={"188970836"}></div>);
  }

  if (type === "billboard") {
    return (<div className="AdsContainer" id={"426407765"}></div>);
  }

  return null;
}
